import fs from 'fs';
import path from 'path';
import matter from 'gray-matter';
import Link from 'next/link';
import Image from 'next/image';

export const metadata = {
  title: 'Blog | My Next.js Site',
  description: 'Read the latest articles and tutorials',
};

function formatDate(date) {
  try {
    return new Date(date).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  } catch (e) {
    return 'Invalid Date';
  }
}

function getReadingTime(content) {
  const wordsPerMinute = 200;
  const words = content.split(/\s+/).length;
  const minutes = Math.ceil(words / wordsPerMinute);
  return `${minutes} min read`;
}

export default function BlogPage() {
  // Get all blog posts
  const blogDir = path.join(process.cwd(), 'src/app/blog/_content');
  const files = fs.readdirSync(blogDir);
  
  const posts = files
    .filter(filename => filename.endsWith('.md'))
    .map(filename => {
      const filePath = path.join(blogDir, filename);
      const fileContent = fs.readFileSync(filePath, 'utf8');
      
      // Use gray-matter to parse the post metadata section
      const { data, content } = matter(fileContent);
      
      return {
        slug: data.slug || filename.replace('.md', ''),
        title: data.title || 'Untitled Post',
        date: data.date || new Date().toISOString(),
        description: data.description || 'No description provided',
        category: data.category || 'Uncategorized',
        image: data.image || '/images/placeholder.jpg',
        author: data.author || { name: 'Anonymous', avatar: '/images/avatar-placeholder.jpg' },
        readingTime: data.readingTime || getReadingTime(content),
      };
    })
    .sort((a, b) => new Date(b.date) - new Date(a.date)); // Sort by date, newest first

  // Group posts by category
  const postsByCategory = posts.reduce((acc, post) => {
    if (!acc[post.category]) {
      acc[post.category] = [];
    }
    acc[post.category].push(post);
    return acc;
  }, {});

  // Filter out empty categories
  const categories = Object.keys(postsByCategory).filter(
    category => postsByCategory[category].length > 0
  );

  return (
    <div className="max-w-7xl mx-auto px-4 py-8">
      {/* Blog Header */}
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold mb-4">Blog</h1>
        <p className="text-lg text-gray-600 max-w-2xl mx-auto">
          Discover stories, thinking, and expertise from writers on any topic.
        </p>
      </div>

      {/* Blog Categories Filter */}
      <div className="flex flex-wrap items-center justify-start gap-4 mb-8 overflow-x-auto pb-2">
        <span className="font-medium text-gray-700">Filter by:</span>
        <button className="px-4 py-2 bg-gray-100 rounded-full text-sm hover:bg-gray-200 transition">
          All Posts
        </button>
        {categories.map(category => (
          <button 
            key={category} 
            className="px-4 py-2 bg-gray-100 rounded-full text-sm hover:bg-gray-200 transition"
          >
            {category}
          </button>
        ))}
      </div>

      {/* Sort Dropdown */}
      <div className="flex justify-end mb-6">
        <div className="relative">
          <select className="bg-white border border-gray-300 rounded-md px-4 py-2 pr-8 appearance-none focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm">
            <option>Sort by: Newest</option>
            <option>Sort by: Oldest</option>
            <option>Sort by: Popular</option>
          </select>
          <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
            <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
              <path d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" />
            </svg>
          </div>
        </div>
      </div>

      {/* No posts message */}
      {posts.length === 0 && (
        <div className="text-center py-20">
          {/* Show nothing when there are no posts as per requirements */}
        </div>
      )}

      {/* Blog posts by category */}
      {categories.map(category => (
        <div key={category} className="mb-16">
          <h2 className="text-2xl font-bold mb-6 pb-2 border-b">{category}</h2>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {postsByCategory[category].map(post => (
              <div key={post.slug} className="blog-post-item bg-white rounded-lg overflow-hidden shadow-sm hover:shadow-md transition duration-300">
                {/* Featured Image */}
                <div className="relative h-48 w-full overflow-hidden">
                  <div className="absolute inset-0 bg-gray-200">
                    {/* Placeholder for actual image */}
                    <div className="w-full h-full flex items-center justify-center text-gray-500">
                      {post.image && (
                        <img 
                          src={post.image} 
                          alt={post.title}
                          className="w-full h-full object-cover"
                        />
                      )}
                    </div>
                  </div>
                </div>
                
                {/* Post Content */}
                <div className="p-5">
                  {/* Category Tag */}
                  <div className="mb-2">
                    <span className="inline-block bg-gray-100 rounded-full px-3 py-1 text-xs font-semibold text-gray-700">
                      {post.category}
                    </span>
                  </div>
                  
                  {/* Post Meta */}
                  <div className="flex items-center justify-between text-xs text-gray-500 mb-2">
                    <span>{formatDate(post.date)}</span>
                    <span>{post.readingTime}</span>
                  </div>
                  
                  {/* Post Title */}
                  <h3 className="text-xl font-bold mb-2 hover:text-blue-600 transition duration-200">
                    <Link href={`/blog/${post.slug}`}>
                      {post.title}
                    </Link>
                  </h3>
                  
                  {/* Post Description */}
                  <p className="text-gray-600 text-sm mb-4 line-clamp-2">{post.description}</p>
                  
                  {/* Author */}
                  <div className="flex items-center">
                    <div className="w-8 h-8 rounded-full overflow-hidden bg-gray-200 mr-3">
                      {post.author.avatar && (
                        <img 
                          src={post.author.avatar} 
                          alt={post.author.name}
                          className="w-full h-full object-cover"
                        />
                      )}
                    </div>
                    <span className="text-sm font-medium">{post.author.name}</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      ))}

      {/* Pagination */}
      {posts.length > 0 && (
        <div className="mt-12 flex justify-center">
          <nav className="flex items-center space-x-1">
            <button className="w-8 h-8 flex items-center justify-center rounded-full border border-gray-300 hover:bg-gray-100">
              <span className="sr-only">Previous</span>
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 19l-7-7 7-7"></path>
              </svg>
            </button>
            <button className="w-8 h-8 flex items-center justify-center rounded-full bg-blue-600 text-white">1</button>
            <button className="w-8 h-8 flex items-center justify-center rounded-full border border-gray-300 hover:bg-gray-100">2</button>
            <button className="w-8 h-8 flex items-center justify-center rounded-full border border-gray-300 hover:bg-gray-100">3</button>
            <button className="w-8 h-8 flex items-center justify-center rounded-full border border-gray-300 hover:bg-gray-100">4</button>
            <button className="w-8 h-8 flex items-center justify-center rounded-full border border-gray-300 hover:bg-gray-100">5</button>
            <button className="w-8 h-8 flex items-center justify-center rounded-full border border-gray-300 hover:bg-gray-100">
              <span className="sr-only">Next</span>
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5l7 7-7 7"></path>
              </svg>
            </button>
          </nav>
        </div>
      )}
    </div>
  );
}